#main-navbar, #main-navbar * {
  box-sizing: border-box;
}

#main-navbar ul {
  margin: 0 !important;
  padding: 0 !important;
  list-style: none !important;
}

#main-navbar li {
  padding: 0 !important;
}

#main-navbar a {
  color: inherit !important;
  font: inherit !important;
  background: none !important;
  border: none !important;
  line-height: normal !important;
  text-decoration: none !important;
}

#main-navbar {
  background-color: var(--card-bg);
  box-shadow: var(--shadow);
  z-index: 20;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

#main-navbar .logo {
  color: var(--text);
  letter-spacing: .04em;
  margin-right: 20px;
  font-family: Montserrat, sans-serif;
  font-size: 1.4rem;
  font-weight: 600;
  text-decoration: none;
}

#main-navbar .nav-links {
  align-items: center;
  gap: 14px;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  display: flex;
}

#main-navbar .nav-links li {
  margin-left: 0;
}

#main-navbar .desktop-nav {
  align-items: center;
  margin-left: auto;
  display: flex;
}

#main-navbar .nav-links a {
  color: var(--muted);
  text-transform: uppercase;
  letter-spacing: .03em;
  padding: 18px 0;
  font-size: .95em;
  font-weight: 400;
  text-decoration: none;
  transition: color .2s, font-weight .2s;
  display: block;
  position: relative;
}

#main-navbar .nav-links a:after {
  content: "";
  transform-origin: 0;
  background-color: #0000;
  height: 3px;
  transition: background-color .2s, transform .2s;
  position: absolute;
  bottom: 8px;
  left: 0;
  right: 0;
  transform: scaleX(0);
}

#main-navbar .nav-links a.active {
  color: var(--accent);
  font-weight: 600;
}

#main-navbar .nav-links a.active:after {
  background-color: var(--accent);
  transform: scaleX(1);
}

#main-navbar .nav-links a:hover {
  color: var(--accent);
}

#main-navbar .menu-toggle {
  cursor: pointer;
  z-index: 20;
  background: none;
  border: none;
  flex-direction: column;
  justify-content: space-around;
  width: 30px;
  height: 25px;
  padding: 0;
  display: none;
}

#main-navbar .menu-toggle .line {
  background-color: var(--text);
  transform-origin: 0;
  width: 100%;
  height: 3px;
  transition: all .3s linear;
}

#main-navbar .menu-toggle.open .line:first-child {
  width: 120%;
  transform: rotate(45deg);
}

#main-navbar .menu-toggle.open .line:nth-child(2) {
  opacity: 0;
  transform: translateX(20px);
}

#main-navbar .menu-toggle.open .line:nth-child(3) {
  width: 120%;
  transform: rotate(-45deg);
}

#main-navbar .mobile-nav {
  background-color: var(--card-bg);
  width: 100%;
  box-shadow: var(--shadow);
  z-index: 10;
  height: 0;
  transition: height .3s ease-in-out;
  display: block;
  position: absolute;
  top: 60px;
  left: 0;
  overflow: hidden;
}

#main-navbar .mobile-nav.open {
  height: auto;
}

#main-navbar .mobile-nav .nav-links {
  text-align: center;
  flex-direction: column;
  gap: 8px;
  padding: 10px 0;
}

#main-navbar .mobile-nav .nav-links li {
  margin: 0;
}

#main-navbar .mobile-nav .nav-links a {
  border-bottom: 1px solid var(--muted-border);
  padding: 15px;
}

#main-navbar .mobile-nav .nav-links a:last-child {
  border-bottom: none;
}

@media (width <= 768px) {
  #main-navbar .desktop-nav {
    display: none;
  }

  #main-navbar .menu-toggle {
    display: flex;
  }
}

#main-navbar .theme-toggle {
  border: 1px solid var(--muted-border);
  color: inherit;
  cursor: pointer;
  background: none;
  border-radius: 6px;
  align-items: center;
  gap: 6px;
  margin-left: 3vw;
  padding: 6px 8px;
  font-size: .9rem;
  display: inline-flex;
}

#main-navbar .theme-toggle .theme-icon {
  width: 16px;
  height: 16px;
  display: inline-block;
}

#main-navbar .mobile-nav .theme-toggle {
  width: calc(100% - 2rem);
  margin: .5rem 1rem;
}

#main-navbar {
  z-index: 9999;
}
